import React, { Component } from 'react';
import { is, fromJS } from 'immutable';
import './LeftNav.scss';
import PropTypes from 'prop-types';
import {
    NavLink
} from 'react-router-dom';
import { Layout, Menu, Icon } from 'antd';
const { Sider } = Layout;

export default class LeftNav extends Component{
    static propTypes = {
        currentPage: PropTypes.string.isRequired,
        defaultCoin: PropTypes.string
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    }
    render(){
        const { currentPage,defaultCoin } = this.props;
        return (
            <div className="left_nav">
                <Layout className="layout">
                    <Sider
                        width={100}
                        className="layout-sider"
                        breakpoint="lg"
                        collapsedWidth="0"
                        onCollapse={(collapsed, type) => { console.log(collapsed, type); }}
                    >
                        <div className="logo" />
                        <Menu theme="dark" mode="inline" defaultSelectedKeys={[currentPage]}>
                            <Menu.Item key="market">
                               <NavLink to="/market">
                                   <Icon type="area-chart" />
                                   <span className="nav-text">市场</span>
                               </NavLink>
                            </Menu.Item>
                            <Menu.Item key="deal">
                                <NavLink to={"/deal/" + defaultCoin}>
                                    <Icon type="pay-circle-o" />
                                    <span className="nav-text">兑换</span>
                                </NavLink>
                            </Menu.Item>
                            <Menu.Item key="shop">
                                <NavLink to="/shop/shop">
                                    <Icon type="appstore-o" />
                                    <span className="nav-text">分号</span>
                                </NavLink>
                            </Menu.Item>
                            <Menu.Item key="my">
                                <NavLink to="/my">
                                    <Icon type="user" />
                                    <span className="nav-text">我的</span>
                                </NavLink>
                            </Menu.Item>
                            <Menu.Item key="safe">
                                <NavLink to="/safe">
                                    <Icon type="safety" />
                                    <span className="nav-text">安全</span>
                                </NavLink>
                            </Menu.Item>
                            <Menu.Item key="quit">
                                <a>
                                    <Icon type="poweroff" />
                                    <span className="nav-text">退出</span>
                                </a>
                            </Menu.Item>
                        </Menu>
                    </Sider>
                </Layout>
            </div>
        )
    }
}


